@import url('https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,600,700&display=swap" rel="stylesheet');
 body{
     padding: 0;
     margin: 0;
     background: #333;
     font-family: 'Montserrat', sans-serif;
}
 :root {
     --royal-blue: rgb(65,105,225);
     --royal-blue-hover: rgb(74,114,234);
     --royal-blue-overlay: rgba(65,105,225,0.9);
     --dark-green: rgb(30,70,30);
     --dark-green-overlay: rgba(30,70,30,0.9);
     --dark-blue: rgb(32,32,100);
     --dark-blue-overlay: rgba(32,32,100,0.9);
     --dark-red: rgb(130,15,15);
     --dark-red-overlay: rgba(130,15,15,0.9);
     --dark-slate-blue: rgb(72,61,139);
     --dark-slate-blue-overlay: rgba(72,61,139,0.9);
     --sea-green: rgb(46,139,86);
     --sea-green-overlay: rgba(46,139,86,0.9);
     --tomato: rgb(255,99,71);
     --tomato-overlay: rgba(255,99,71,0.9);
}
 header{
     display: flex;
     flex-direction: row;
     justify-content: flex-end;
     align-items: center;
     padding: 0 50px;
     background: var(--royal-blue);
}
 header nav{
     height: 100%;
     display: flex;
}
 header nav a{
     color: #fff;
     text-transform: uppercase;
     letter-spacing: 1px;
     font-size: 14px;
     font-weight: 500;
     display: flex;
     align-items: center;
     padding: 0 15px;
     cursor: pointer;
     height: 100%;
}
 header nav a:hover{
     background: #2E56CE;
}
 .logo{
     width: 60px;
     height: 60px;
     overflow: hidden;
}
 .logo img{
     width: 100%;
     height: auto;
     object-fit: cover;
}
 #root{
     width: 100%;
     background: #f6f6f6;
}
 header{
     width: 100%;
     box-sizing: border-box;
     height: 100px;
}
 .section-intro{
     max-height: 500px;
     width: 100%;
     position: relative;
     overflow: hidden;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     background-position: center;
     background-size: cover;
}
 .section-loading{
     height: calc(100vh - 100px);
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-align: center;
     -ms-flex-align: center;
     align-items: center;
     -webkit-box-pack: center;
     -ms-flex-pack: center;
     justify-content: center;
     text-align: center;
}
 .section-intro__header{
     color: var(--royal-blue);
     text-transform: uppercase;
     letter-spacing: 4px;
     font-size: 40px;
}
 .section-intro__container-wrapper{
     height: 60px;
     width: 60px;
     position: absolute;
     background: transparent;
     bottom: -30px;
     left: 50%;
     transform: translate(-50%, 0);
     z-index: 999;
}
 .section-intro__element-outer{
     display: flex;
     width: 60px;
     height: 60px;
     background: #fff;
     transform: rotate(45deg);
}
 .section-intro__element-inner{
     width: 40px;
     height: 40px;
     background: #111;
     margin: 10px 
}
 .card-section{
     width: 100%;
     display: flex;
     justify-content: space-between;
     align-items: center;
     flex-wrap: wrap;
     padding: 50px;
     box-sizing: border-box;
}
 .section-header{
     width: 100%;
     height: 100px;
     background: #EDEDED;
     box-sizing: border-box;
     padding: 0 50px;
     display: flex;
     align-items: center;
     justify-content: space-between;
}
 .portfolio-menu{
     padding: 0;
     margin: 0;
     list-style-type: none;
     display: flex;
     justify-content: space-between;
     color: var(--royal-blue);
     font-size: 16px;
     letter-spacing: 1px;
     font-weight: 500;
     text-transform: uppercase;
     width: 470px;
}
 .portfolio-menu li{
     transition: .1s all;
     cursor: pointer;
     position: relative;
}
 .portfolio-menu li:after, .portfolio-menu li:before{
     content: '';
     position: absolute;
     bottom: -6px;
     height: 2px;
     background: #6666FF;
     transition: .2s all;
}
 .portfolio-menu li:after{
     right: 50%;
     width: 0;
     display: block;
     transform: rotate(180deg);
}
 .portfolio-menu li:hover:after{
     width: 50%;
}
 .portfolio-menu li:hover{
     color: $light-blue;
}
 .portfolio-menu li:before {
     left: 50%;
     width: 0;
     display: block;
     transform: rotate(180deg);
}
 .portfolio-menu li:hover:before{
     width: 50%;
}
 .portfolio-menu .portfolio-menu__link_active {
     color: $light-blue;
}
 .portfolio-menu .portfolio-menu__link_active:after {
     left: 0;
     width: 100%;
}
 .portfolio-menu .portfolio-menu__link_active:before {
     width: 0;
}
 .portfolio-menu .portfolio-menu__link_active:hover:after {
     left: 0;
     width: 100%;
}
 .portfolio-menu .portfolio-menu__link_active:hover:before {
     width: 0;
}
 .active-hashtag{
     background: var(--royal-blue);
     color: #fff;
     padding: 8px 15px;
     display: block;
}
 .active-hashtags-wrapper{
     display: flex;
}
 .active-hashtag{
     cursor: pointer;
}
 .active-hashtag:not(:last-child){
     margin-right: 12px;
}
 .active-hashtag:hover{
     background: var(--royal-blue-hover);
}
 .active-hashtags-selected{
     font-size: 20px;
     margin-left: 30px;
     line-height: 34px;
}
 .section-header-img{
     height: 90px;
     width: 90px;
}
 .section-header-img img{
     object-fit: cover;
     width: 100%;
}
 .clear-button button{
     background: var(--royal-blue);
     color: #fff;
     outline: none;
     border: none;
     padding: 8px 15px;
     font-size: 16px;
     cursor: pointer;
}
 .clear-button button:hover{
     background: var(--royal-blue-hover);
}
 .card{
     height: 500px;
     width: 500px;
     background: #fff;
     position: relative;
     overflow: hidden;
     margin-bottom: 50px;
     transition-duration: .5s;
}
 .card:hover{
     transform: scale(1.04);
     transition-duration: .5s;
     box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

