 .card .card-icon-wrapper svg{
     animation: title_animate_out 0.8s linear 1;
     animation-fill-mode: forwards;
     animation-delay: .25s ease;
     font-size: 30px;
}
 .card:hover .card-icon-wrapper svg{
     animation: title_animate 0.8s linear 1;
     animation-fill-mode: forwards;
}
 .card-icon{
     position: absolute;
     top: 50%;
     left: 50%;
     height: 450px;
     width: 450px;
     background: transparent;
     color: #fff;
     transform: translate(-50%,-50%);
     z-index: 2;
}
 .card-icon-wrapper{
     position: relative;
     width: 100%;
     height: 100%;
     overflow: hidden;
     display: flex;
     justify-content: center;
     align-items: center;
     text-transform: uppercase;
     font-size: 16px;
     font-weight: 600;
     letter-spacing: 1px;
}
 .card-icon-wrapper p{
     margin: 0;
}
 div.card-image{
     overflow: hidden;
     height: 240px;
}
 .card-image img{
     object-fit: cover;
     width: 100%;
}
 .card-description{
     padding: 20px;
     height: 260px;
     box-sizing: border-box;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
}
 .card-description h2{
     text-align: center;
     text-transform: uppercase;
     font-size: 16px;
     margin-top: 0;
     letter-spacing: 0.5px;
}
 .card-description p{
     font-size: 14px;
     line-height: 25px;
}
 .card-hashtags{
     color: var(--royal-blue);
     display: flex;
     z-index: 1;
     position: relative;
}
 .card-hashtags span{
     display: block;
     cursor: pointer;
     font-size: 14px;
}
 .card-hashtags span:hover{
     cursor: pointer;
}
 .card-hashtags span:not(:first-child){
     margin-left: 12px;
}
 .card-button, .main-button{
     background: var(--royal-blue);
     color: #fff;
     text-decoration: none;
     text-transform: uppercase;
     font-size: 14px;
     font-weight: 500;
     padding: 10px 15px;
     width: 140px;
     text-align: center;
     display: block;
     margin: 20px auto 0;
     cursor: pointer;
     position: relative;
     z-index: 1;
     transition: .2s all;
}
 .card-button:hover{
     text-decoration: none;
     color: #fff;
     background: #3354CA;
}
 .card:hover .card-button, .card:hover .card-hashtags{
     animation: button_animate_in 0.1s linear 1;
     animation-fill-mode: forwards;
     animation-delay: 0.5s;
}
 .card .card-button, .card .card-hashtags{
     animation: button_animate_out 0.1s linear 1;
     animation-fill-mode: forwards;
     animation-delay: 0.5s;
}
 footer{
     background: #222;
     height: 100px;
     width: 100%;
     padding: 0 50px;
     box-sizing: border-box;
     display: flex;
     justify-content: space-between;
     align-items: center;
}
 footer p{
     color: var(--royal-blue);
     margin: 0;
}
 @keyframes button_animate_in{
     from{
         z-index: 1;
    }
     to{
         z-index: 2;
    }
}
 @keyframes button_animate_out{
     from{
         z-index: 2;
    }
     to{
         z-index: 1;
    }
}
 @keyframes title_animate{
     from{
         opacity: 1;
    }
     to{
         opacity: 0;
    }
}
 @keyframes title_animate_out{
     from{
         opacity: 0;
    }
     to{
         opacity: 1;
    }
}
 @keyframes top_animate{
     from{
         border-width: 0 500px 500px 0;
    }
     to{
         border-width: 0 50px 50px 0;
    }
}
 @keyframes top_animate_again{
     from{
         border-width: 0 0 0 0;
    }
     to{
         border-width: 0 500px 500px 0;
    }
}
 @keyframes bottom_animate{
     from{
         border-width: 500px 0 0 500px;
    }
     to{
         border-width: 50px 0 0 50px;
    }
}
 @keyframes bottom_animate_again{
     from{
         border-width: 50px 0 0 50px;
    }
     to{
         border-width: 500px 0 0 500px;
    }
}
 .card-icon-wrapper span{
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     display: block;
     pointer-events: none;
}
 .card-icon-wrapper span:before{
     content: '';
     position: absolute;
     width: 100%;
     height: 3px;
     background: #fff;
     transform-origin: left;
     transition: 0.5s;
}
/* carousel */
 .section-intro{
     overflow: hidden;
}
 .carousel.carousel-slider{
     height: 100%;
}
 .carousel-item{
     position: relative;
     overflow: hidden;
}
 .carousel-item img{
     display: block;
     object-fit: cover;
     min-height: 500px;
     max-height: 500px;
}
 .carousel-overlay{
     background: rgba(0,0,0,0.8);
     opacity: 1;
     position: absolute;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
}
 .carousel-description{
     width: 400px;
     height: 200px;
     color: #fff;
     vertical-align: middle;
}
 .carousel-description h2{
     font-weight: 300;
     color: #fff;
     text-transform: uppercase;
     font-size: 28px;
     letter-spacing: 3px;
}
 .carousel-description p{
     line-height: 30px;
}
 .carousel .slide{
     background: transparent !important;
}
